import React from 'react';
import { NavBar, BHPLogo } from 'bhp-ui-react';
import { Button, Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

export default class Example extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      dropdownOpen: false
    };
  }

  toggle() {
    this.setState({
      dropdownOpen: !this.state.dropdownOpen
    });
  }
  render() {
    return (
      <div className="">
        <NavBar
          containerStyle="navbar-expand-lg navbar-dark bg-bhp-orange-1"
          toggleButton={<span className='navbar-toggler-icon'/>}
          bodyStyle="navbar-collapse"
          staticComponent={<BHPLogo className='align-middle' color='white' />}
        >
          <ul className="navbar-nav mr-auto" >
              <li className="nav-item active">
                <a className="nav-link" >Home <span className="sr-only">(current)</span></a>
              </li>
              <li className="nav-item">
                <a className="nav-link" >Link</a>
              </li>
              <li className="nav-item dropdown">
                <Dropdown isOpen={this.state.dropdownOpen} toggle={this.toggle}>
                <DropdownToggle color="primary" caret>
                  Dropdown button
                </DropdownToggle>
                <DropdownMenu>
                  <DropdownItem>Action</DropdownItem>
                  <DropdownItem>Another Action</DropdownItem>
                  <DropdownItem>Something else here</DropdownItem>
                </DropdownMenu>
              </Dropdown>
              </li>
              <li className="nav-item">
                <a className="nav-link disabled" >Disabled</a>
              </li>
          </ul>
          <form className="form-inline my-2 my-lg-0">
            <input className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search" />
             <Button color="primary" >Search</Button>{' '}
          </form>
        </NavBar>
      </div>
    );
  }
};



